import React from "react";
import "./AvailabelQuanity.scss";

class AvailabelQuanity extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0
    };
  }

  increment = () => {
    this.setState({
      value:
        this.state.value < this.props.maxValue
          ? ++this.state.value
          : this.state.value
    });
  };

  decrement = () => {
    this.setState({
      value: this.state.value > 0 ? --this.state.value : 0
    });
  };
  componentDidMount() {
    this.setState({
      maxValue: this.props.maxValue
    });
  }
  handleChange = event => {
    this.setState({ value: event.target.value });
    this.props.onQuanityChange(this.state.value);
  };

  render() {
    return (
      <div>
        <div className="quantity-input">
          <input
            className="quantity-input__screen"
            type="text"
            value={this.state.value}
            readOnly
            onChange={this.handleChange}
          />
          <button className="quantity-input-increment" onClick={this.increment}>
            +
          </button>
          <button className="quantity-input-decrement" onClick={this.decrement}>
            &mdash;
          </button>
        </div>
      </div>
    );
  }
}

export default AvailabelQuanity;
