import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Scrollbars } from "react-custom-scrollbars";
import ItemListHeader from "./../../ItemsListHeader";
import Search from "./../../UI/Search";
import { ItemEquipmentAssign } from "./../../ItemDetailsRow";
import {
  activeWorkerEquipmentAdd,
  onRemoveEquipmentFromWorker
} from "./../../../actions";

const WorkerEquipmentListContainer = props => {
  useEffect(() => {
    props.activeWorkerEquipmentAdd();
  }, []);

  return (
    <div className="ItemsList-wrapp">
      <Search />
      <div className="items-list">
        <ItemListHeader
          classname="item-row-equipments"
          names={["", "Model", "Category", "Assigned", "Available", ""]}
        />
        <Scrollbars
          style={{ height: 360 }}
          autoHide
          autoHideTimeout={1000}
          autoHideDuration={200}
          className="items-box"
        >
          {props.activeWorkerEquip.map(i => {
            const item = i;
            return (
              <ItemEquipmentAssign
                id={item.id}
                key={item.id}
                image={item.img}
                name={item.name}
                model={item.model}
                category={item.category}
                assigned={item.assigned}
                available={item.available}
                onRemoveItem={props.onRemoveEquipmentFromWorker}
              />
            );
          })}
        </Scrollbars>
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    activeWorkerEquip: state.activeWorkerEquip,
    activeUserId: state.activeUserId
  };
};
const mapDispatchToProps = {
  activeWorkerEquipmentAdd,
  onRemoveEquipmentFromWorker
};
// const mapDispatchToProps = dispatch => ({
//   activeWorkerEquipmentAdd: () =>
//     dispatch({
//       type: "ADD_ACTIVE_WORKER_EQUIPMENT"
//     })
// });

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WorkerEquipmentListContainer);
